html {
  height: 100%;
}

body {
  font-family: 'Proxima Nova', sans-serif;
  font-weight: 400;
  background: $white;
  font-size: 16px;
  color: $black;
  line-height: 1.3;
  height: 100%;
}

.blocker {
  z-index: 1000 !important;
}

.container-fluid {
  display: flex;
  flex-direction: column;
}

input:focus,
input:active,
textarea:focus,
textarea:active,
button:focus,
button:active,
span:focus,
span:active,
button:focus,
button:active {
  outline: none;
}

select:focus,
select:hover,
option:hover,
option:focus {
  outline: none;
}

.preload {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100000000;
  background: #fff;
}

.offset-two-side {
  padding: 60px 0;
}

.wrap-circle {
  background: #000;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999999999;
  transition: all .3s ease-in-out;
  &.hide {
    transform: scale(0);
    opacity: 0;
    visibility: hidden;
  }
}
.circle{
  position:relative;
  width:100px;
  height:100px;
  svg{
    fill:none;
    stroke:#8a3ab9;
    stroke-linecap: round;
    stroke-width:3;
    stroke-dasharray: 1;
    stroke-dashoffset: 0;
    animation: stroke-draw 6s ease-out infinite alternate;
    // animation: stroke-draw 6s cubic-bezier(0.77, 0, 0.175, 1) infinite alternate;
  }
  img{
    position:absolute;
    left:50%;
    top:50%;
    transform:translate(-50%, -50%);
    width:70px;
    border-radius:50%;
    filter: invert(1);
    height: 70px;
  }
}
@keyframes stroke-draw {
  from{
    stroke:#8a3ab9;
    stroke-dasharray: 1;
  }
  to{
    stroke:#cd486b;
    transform:rotate(180deg);
    stroke-dasharray: 8;
  }
}

.title-s {
  text-align: center;
  font-size: 32px;
  color: $black;
  font-family: 'Lobster', cursive;
}
