//Logo
.logo-wrap {
  .logo {
    display: flex;
    align-items: center;
    text-decoration: none;

    img {
      height: 40px;
    }

    .name-site {
      font-family: 'Billabong', sans-serif;
      padding-left: 15px;
      font-size: 34px;
      color: $black;
    }
  }
}
//end Logo

.direct {
  position: absolute;
  right: 30px;
  bottom: 10px;
  cursor: pointer;
  z-index: 1000;
  width: 26px;
  height: 26px;
  &:before {
    content: '';
    position: absolute;
    background: url(../images/direct.png) no-repeat;
    -webkit-background-size: cover;
    background-size: cover;
    width: 33px;
    height: 33px;
  }
}